import React, { useState, useRef, useEffect } from "react";
import Controls from "./Controls";
import Details from "./Details";
import SearchBar from "./SearchBar";


function Player(props) {
  const audioEl = useRef(null);
  const [isPlaying, setIsPlaying] = useState(false);

  useEffect(() => {
    if (isPlaying) {
      audioEl.current.play();
    } else {
      audioEl.current.pause();
    }
  });

  const [filteredSongs, setFilteredSongs] = useState(props.songs);

  const handleSearch = (query) => {
    const filtered = props.songs.filter(
      (song) =>
        song.title.toLowerCase().includes(query.toLowerCase()) ||
        song.artist.toLowerCase().includes(query.toLowerCase())
    );
    setFilteredSongs(filtered);
  };
  


  const SkipSong = (forwards = true, newIndex = null) => {
    if (newIndex !== null) {
      props.setCurrentSongIndex(newIndex);
    } else {
      if (forwards) {
        props.setCurrentSongIndex((prevIndex) => (prevIndex + 1) % props.songs.length);
      } else {
        props.setCurrentSongIndex((prevIndex) => (prevIndex - 1 + props.songs.length) % props.songs.length);
      }
    }
  };
  

  return (
    <div className="main">
      <div className="left">
        <h1>Melophile</h1>
        <h3>PLAYLIST</h3>
        <SearchBar handleSearch={handleSearch} />
        <ul>
          {filteredSongs.map((song, index) => (
            <li key={index} onClick={() => SkipSong(true, index)}>
              {song.title}
            </li>
          ))}
        </ul>
      </div>

      <div className="c-player">
        <div className="player-left">
          <Details song={props.songs[props.currentSongIndex]} />
        </div>
        <div className="player-right">
          <div className="player-top">
            <p>
              Next up: {" "}
              <span>
                {props.songs[props.nextSongIndex].title} by{" "}
                {props.songs[props.nextSongIndex].artist}
              </span>
            </p>
          </div>
          <div className="player-mid">
            <Controls
              isPlaying={isPlaying}
              setIsPlaying={setIsPlaying}
              SkipSong={SkipSong}
            />
          </div>
          <div className="player-bottom">
            <audio
              className="c-player--audio"
              src={props.songs[props.currentSongIndex].src}
              ref={audioEl}
              controls
              autoPlay
            ></audio>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Player;
